/* Import Poppins Font */
@font-face {
    font-family: 'Poppins';
    src: url('./fonts/Poppins/Poppins-Black.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: 'Poppins';
    src: url('./fonts/Poppins/Poppins-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}




/* Import Halant Font */
@font-face {
font-family: 'Halant';
src: url('./fonts/Halant/Halant-Regular.ttf') format('truetype');
font-weight: normal;
font-style: normal;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --pickled-bluewood: #2D394B;
    --jordy-blue: #9CC9F5;
    --regent-gray: #8A94A4;
    --white: #ffffff;
    --japanese-Laurel: #00A3FF;
    --green: #31AF36;
    --spindle: #B3C8E7;
    --malachite: #FFCC33;
    --black: #101010;
    --silver: #B8B8B8;
    --boulder: #757575;
    --gull-gray: #99a6b960;
    --cerulean: #00A0F0;
    --dove-gray: #b8b8b8;
}



html{
    background-color: var(--black);
}

body {
    font-family: 'Halant', serif; /* Use Poppins font for the body */
    color: var(--white);
    font-size: clamp(1rem, 1.125rem, 1.25rem);
    font-style: normal;
    font-weight: 400;
    line-height: 27px; /* 150% */
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    line-height: 1.2;
}

h1 {
    font-size: clamp(3rem, 5rem, 8rem);
    font-weight: 900;
    font-style: normal;
}


h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    position: relative;
    padding-bottom: 20px;
    background-image: url('./img/leave.png');
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 80px;
}

h3 {
    font-size: 1.175rem;
}

p{
    max-width: 720px;
    width: 100%;
}



.section{
    max-width: 1440px;
    width: 100%;
    padding: 100px 0;
    margin-left: auto;
    margin-right: auto;
}

.container {
    max-width: 1280px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 0 32px;
    
}

.wrapper {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.title-center {
    text-align: center;
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin-bottom: 50px;
}

.hide {
    display: none;
}


.row{
    display: flex;
}

.text{
    color: white;
}

.space-between {
    justify-content: space-between;
}

.btn {
    display: flex;
    width: fit-content;
    padding: 8px 16px;
    background-color: var(--malachite);
    color: var(--black);
    letter-spacing: 3px;
    text-transform: uppercase;
    line-height: 27px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 18px;
    transition: all .3s ease-in-out;
    cursor: pointer;
    font-weight: 900;
    border-radius: 4px;
    border: 1px solid var(--malachite);
}

.btn:hover {
    background-color: var(--silver);
    color: var(--white);
}
    
ul{
    list-style: none;
}





/* header style */
header {
    position: fixed;
    z-index: 50;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1440px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    transition: background-color 0.3s ease;
    padding: 16px 0;
}

header.scrolled {
    background-color: black;
}

header .container {
    align-items: center;
}

header #logo{
    margin-right: 2rem;
    display: flex;
    align-items: center;
}
header #openmenu, .closemenu {
    height: fit-content;
    display: none;
}
header #openmenu i{
    color: var(--white);
    font-size: 30px;
}
header #openmenu i:hover{
    color: var(--malachite);
}

header ul{
    align-items: center;
    font-size: clamp(0.75rem, 4vw, 1.5rem );
    padding-top: 1rem;
    padding-bottom: 1rem;
    justify-content: space-between;
    width: 100%;
}

header ul li{
    color: var(--white);
    transition: all .3s ease-in-out;
    padding: 0.5rem 0.5rem;
    cursor: pointer;
    font-family: 'Poppins';
    font-weight: 900;
    font-size: 18px;
    margin-left: 50px ;
}

header ul li:hover{
    color: var(--malachite);
}

footer#footer{
    background-color: var(--black);
    padding: 1.5rem 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0.875rem

}

footer#footer a{
    color: var(--white);
    transition: all .3s ease-in-out;
    text-decoration: none;
    
}
footer#footer a:hover{
    color: var(--malachite);
}

.logo {
height: 3rem;
width: auto;
}
.active {
    color: var(--malachite); /* Change this to your desired active color */
  }

/* WELCOME BADGE STYLE */

#welcome-badge{
    position: fixed;
    bottom: 0;
    right: 16px;
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 281px;
    max-height:128px;
    width: 100%;
    height: 100%;

}
#welcome-badge #box{
    width: 100%;
    height: 100%;
}
#welcome-badge .image-container{
    background-image: url(./img/Welcome-badge.png);
    background-size: cover;
    width: 100%;
    height: 100%;
}

#welcome-badge .badgeClose{
    font-family: 'poppins';
    position: absolute;
    top: -25px;
    right: 8px;
    cursor: pointer;
    background-color: var(--white);
    color: var(--black);
    border-radius: 50px;
    width: 20px;
    height: 20px;
    display: flex;
    place-content: center;
    
}

#welcome-badge .badgeClose p{
    font-size: 16px;
    text-align: center;
    line-height: 20px;
}

/* Home */

/* HERO SECTION */
#home{
    background-image: url(./img/hero-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: top center;
    -webkit-box-pack: center;
    min-height: 750px;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
#home .content{
    max-width: 590px;
    width: 100%;
}

#home .content > *:not(:last-child){
    margin-bottom: 30px;
}

/* SPECIAL DISH SECTION */
#special-dishes{
    background-color: var(--black);
}

#special-dishes p {
    color: var(--dove-gray);
}

#special-dishes div.owl-carousel{
    overflow-x: auto;
    transition: transform 0.5s ease-in-out; /* Add smooth transition */
    scroll-snap-type: x mandatory;

}

#special-dishes div.owl-carousel .item{
    display: block;
    flex-direction: column;
    width: calc(100% / 4 - 20px);
    align-items: flex-start;
    gap: 30px;
    flex-shrink: 0;    
    text-align: center;

}

#special-dishes div.owl-carousel .item img{
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    object-fit: cover;
    -o-object-position: center;
    margin-bottom: 30px;
}


#special-dishes .special-dish-content *:not(:last-child){
    margin-bottom: 20px;
}

#special-dishes div.owl-carousel .item:not(:last-child){
    margin-right: 20px;
}

#special-dishes .special-dish-content p.price{
    color: var(--malachite);
    font-family: 'poppins';
    font-weight: 900;
    margin-bottom: 20px;
}


@media screen and (max-width: 992px) {
    h1{
        text-align: center;
    }
    
    h2{
        font-size: 2.5rem;
    }

    .container {
    
    padding: 0 16px;
    
}
    
    /* HOME */

    #home .content {
        max-width: 100%;
        text-align: center;
    }

    #home .content .btn {
        margin-left: auto;
        margin-right: auto;
    }
    
    header .nav{
        display: flex;
        flex-direction: column;
        position: fixed;
        background-color: var(--black);
        top: 0;
        left: -320px;
        width: 320px;
        height: 100vh;
        z-index: 10;
        transform: translateX(-100%);
        transition: transform 0.5s ease-in-out;
        padding: 30px;

    }
    header .nav #navList{
        display: flex;
        flex-direction: column;
        
    }

    header #openmenu, .closemenu {
        height: fit-content;
        display: block;
        color: var(--white);
        text-decoration: none;
    }

    .closemenu:hover{
        color: var(--malachite);
    }
    .closemenu i {
        font-size: 30px;
        display: block;
        padding: 8px;
        text-align: end;
    }

    header ul{
        align-items: start;
        font-size: clamp(0.75rem, 4vw, 1.5rem );
        padding-top: 1rem;
        padding-bottom: 1rem;
        justify-content: space-between;
        width: 100%;
    }
    
    header ul li{
        width: 100%;
        margin-left: 0 ;
        margin-bottom: 20px;
    }

    /* SPECIAL DISH SECTION */
    #special-dishes div.owl-carousel .item{
        width: calc(100% / 2 - 12px);
    }
}

@media screen and (max-width: 600px) {
    h1{
        font-size: clamp(1rem, 3.123rem, 50px);
    }
    h2{
        font-size: 1.875rem;
    }
    
   

    /* SPECIAL DISH SECTION */
    #special-dishes div.owl-carousel .item{
        width: calc(100% / 1 - 10px);
    }
}